import { MENU_LIST, USER_INFO } from './const'

export default {
    [MENU_LIST] (state, payload) {
        const menus = payload.data.listAuth
        sessionStorage.setItem('menus', menus ? JSON.stringify(menus) : '')
        state.menus = menus || {}
    },
    [USER_INFO] (state, payload) {
        const userInfo = payload.data
        sessionStorage.setItem('userInfo', userInfo ? JSON.stringify(userInfo) : '')
        state.userInfo = userInfo || {}
    }
}