import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

// TODO: 兼容老系统
const menus = sessionStorage.getItem('menus') || sessionStorage.getItem('listAuth_data')
const userInfo = sessionStorage.getItem('userInfo') || sessionStorage.getItem('user_data')

export default new Vuex.Store({
    state: {
        'menus': menus ? JSON.parse(menus) : {},
        'userInfo': userInfo ? JSON.parse(userInfo) : {}
    },
    mutations,
    actions
})
