import moment from 'moment'
moment.locale('zh-cn')

const formatMoney = function (val, type) {
    if (val === '' || val === 0) return '0.00'
    val = Number(val)
    if (isNaN(val)) return '-'
    return val.toFixed(type).replace(/(\d)(?=(\d{3})+\.)/g, '$1,')
}

const formatDate = function (time, param) {
    if (param) {
        return moment(time).format(param)
    }
    return moment(time).format('YYYY-MM-DD HH:mm')
}

export default {
    formatMoney,
    formatDate
}