import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from '@/store/index'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import '@/api/axios.js'
import VueQuillEditor from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import '@/assets/style/common.scss'
import '@/assets/style/base/scrollbar.scss'
import 'prismjs/themes/prism.css'
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import PExample from '@/components/PExample.vue'
import filters from './utils/filters'
import preview from 'vue-photo-preview'
import 'vue-photo-preview/dist/skin.css'
let options = {
    fullscreenEl: false
}
Vue.use(preview, options)

Vue.config.productionTip = false

Vue.use(ElementUI)
Vue.use(VueQuillEditor)

Vue.prototype.$eventBus = new Vue()

Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key])
})

Vue.component('PAttrTable', PAttrTable)
Vue.component('PEventTable', PEventTable)
Vue.component('PExample', PExample)

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')