import axios from 'axios'
import qs from 'qs'
import {
    interfaceUrl
} from './config.js'
import {
    Message
} from 'element-ui'

axios.defaults.baseURL = interfaceUrl
// 由于后端暂时不接受json格式的数据，暂时需要设置content-type
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.headers['AccessKeyId'] = 'f3afb55c6795'
axios.defaults.headers['AccessKeySecret'] = '92ab79e8980cbe0c6bfef8b9733d3a1b'
axios.interceptors.request.use(function (config) {
    // 登录token带到请求的头部中，用于校验登录状态
    // const token = sessionStorage.getItem('token')
    // if (token) {
    //     config.headers['Authorization'] = token
    // }
    // 由于后端不支持json数据传输，在提交数据之前进行数据转换处理
    const method = config.method.toLowerCase()
    if (method === 'post' || method === 'put' || method === 'patch') {
        config.data = qs.stringify(config.data)
    }
    return config
}, function (error) {
    return Promise.reject(error)
})

axios.interceptors.response.use(function (response) {
    return response
}, function (error) {
    // TODO: 异常统一处理
    Message({
        message: error.response.data.message[0] || error.response.message
    })
    return Promise.reject(error)
})

export default axios
